@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.ttf?1u6yil") format("truetype"),url("../fonts/icomoon.woff?1u6yil") format("woff"),url("../fonts/icomoon.svg?1u6yil#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "";
}

.icon-instagram:before {
  content: "";
}

.icon-twitter:before {
  content: "";
}

.icon-linkedin:before {
  content: "";
}

body {
  font-family: "Roboto","Helvetica","Arial",sans-serif;
}

h3 {
  font-size: calc(1rem + .6vw);
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

.xs-visible {
  display: none;
}

.btn {
  display: block;
  color: #fff;
  text-shadow: 1px 1px 0 rgba(0,0,0,.21);
  text-transform: uppercase;
  background: linear-gradient(45deg, #1EACC7 0%, #4ce2ff 100%);
  box-shadow: inset 0 -3px 0 rgba(0,0,0,.12);
  border: none;
  border-radius: 4px;
}

.title {
  margin: 0 auto;
  padding: 1px 6px;
  font-size: 11px;
  text-transform: uppercase;
}

.title__wrapper {
  display: flex;
  margin-bottom: 30px;
}

.title_dark {
  color: #fff;
  background-color: #000;
}

.title_light {
  color: #000;
  background-color: #fff;
}

.section-heading {
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}

.section-heading_dark {
  color: #222;
}

.section-heading_light {
  color: #fff;
}

.section-subheading {
  font-size: 17px;
  text-align: center;
  color: #fff;
  margin: 0;
}

.section-subheading_dark {
  color: #222;
}

.section-subheading__light {
  color: #fff;
}

.header {
  position: relative;
}

.header .nav {
  position: fixed;
  width: 100%;
  z-index: 5000;
  color: #fff;
  height: 52px;
  background-color: #212121;
}

.header .nav .menu {
  display: flex;
  justify-content: space-between;
  padding: 14px 70px;
  margin: 0;
}

.header .nav .menu__item:not(:last-child) {
  border-right: 2px dotted #000;
  padding-right: 46px;
}

.header .nav .menu__link {
  font-weight: 300;
  font-size: 15px;
  color: #fff;
  border-bottom-width: 0;
}

.header .nav .menu__link::after {
  content: "";
  display: block;
  height: 1px;
  width: 0;
  background-color: #fff;
  transition: .3s width;
}

.subheader {
  position: absolute;
  width: 100%;
  top: 52px;
  color: #fff;
  padding: 16px 0;
  height: 90px;
  background-color: rgba(0,0,0,.6);
}

.subheader__official {
  font-weight: 300;
  font-size: 13px;
  opacity: .6;
}

.subheader .logo__link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: .5rem;
}

.subheader .logo__link:hover {
  color: #1fbad6;
}

.subheader .logo__img {
  width: 44px;
  height: 44px;
  flex: 0 0 auto;
}

.subheader .logo__img img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.subheader .logo__desc {
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
}

.subheader .call {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  height: 100%;
}

.subheader .call__text {
  font-weight: 300;
  font-size: 12px;
}

.subheader .call__link {
  font-size: 22px;
}

.subheader .call__link:hover {
  color: #1fbad6;
}

.subheader__btn-wrap {
  display: flex;
  height: 100%;
  align-items: center;
}

.subheader__btn {
  width: 100%;
  height: 46px;
  font-size: 14px;
}

.hamburger {
  display: none;
}

.promo {
  min-height: 100vh;
  background: url("../images/bg/promo_bg.jpeg") center center/cover no-repeat fixed;
  color: #fff;
  text-align: center;
  padding: 202px 0 135px;
}

.promo__header {
  font-weight: 900;
  margin: 0 0 16px;
  text-transform: uppercase;
}

.promo__subheader {
  font-weight: 700;
  margin: 0 0 24px;
  text-transform: uppercase;
}

.promo__btn {
  margin: 120px auto 0;
  width: 260px;
  height: 62px;
}

.promo-desc__paragraph {
  font-size: 18px;
}

.reasons {
  min-height: 780px;
  padding: 20px 0 60px;
}

.reasons__items {
  margin-top: 32px;
}

.reasons__item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 34px;
}

.reasons .item__round {
  flex: 0 0 116px;
  width: 116px;
  height: 116px;
  margin-right: 25px;
  background: #1eacc7;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.reasons .item__img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.reasons .item__body {
  align-self: flex-start;
}

.reasons .item-body__heading {
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
  color: #1eacc7;
}

.reasons .item-body__text {
  font-weight: 300;
  font-size: 15px;
  line-height: 21px;
}

.mobile {
  min-height: 864px;
  padding: 48px 0 80px;
  background: url("../images/bg/mobile_bg.jpeg") center center/cover no-repeat;
}

.mobile__cards {
  margin-top: 30px;
}

.mobile-card {
  position: relative;
  height: 280px;
  width: 320px;
  margin: 0 auto 20px;
  padding: 80px 16px 0;
  border-radius: 6px;
}

.mobile-card_1 {
  background: url("../images/cards/card_1.jpeg") center center/cover no-repeat;
}

.mobile-card_2 {
  background: url("../images/cards/card_2.jpeg") center center/cover no-repeat;
}

.mobile-card_3 {
  background: url("../images/cards/card_3.jpeg") center center/cover no-repeat;
}

.mobile-card_4 {
  background: url("../images/cards/card_4.jpeg") center center/cover no-repeat;
}

.mobile-card_5 {
  background: url("../images/cards/card_5.jpeg") center center/cover no-repeat;
}

.mobile-card_6 {
  background: url("../images/cards/card_6.jpeg") center center/cover no-repeat;
}

.mobile-card__head {
  line-height: 36px;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
}

.mobile-card__link {
  position: absolute;
  bottom: 34px;
  left: 50%;
  transform: translateX(-50%);
  width: 29px;
  height: 29px;
  background-color: #000;
  border-radius: 50%;
}

.mobile-card__link::before,
.mobile-card__link::after {
  content: "";
  display: block;
  width: 17px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #1eacc7;
}

.mobile-card__link::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.choice {
  position: relative;
  padding: 60px 0 120px;
  background: url("../images/bg/mobile_bg.jpeg") center center/cover no-repeat;
}

.choice__black-bg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 502px;
  background-color: #070716;
}

.choice__title,
.choice__heading,
.choice__subheading {
  position: relative;
  z-index: 20;
}

.choice__subheading {
  margin-bottom: 58px;
}

.choice__img {
  position: relative;
}

.choice__img img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.choice__text {
  font-weight: 300;
  font-size: 17px;
  line-height: 28px;
  color: #222;
  text-align: center;
  margin: 0 auto;
}

.require {
  padding: 60px 0 80px;
}

.require__heading {
  margin-bottom: 30px;
}

.require__item {
  margin-bottom: 4rem;
}

.require-item__title {
  font-size: 17px;
}

.require-item__title span {
  font-size: 22px;
  text-transform: uppercase;
}

.require-item__title_blue {
  color: #1eacc7;
}

.require-item__title_red {
  color: #ff5656;
}

.require-item__subtitle {
  font-weight: 300;
  font-size: 17px;
  color: #1eacc7;
}

.require-list {
  padding-left: 50px;
}

.require-list__item {
  position: relative;
  font-weight: 300;
  font-size: 17px;
  line-height: 24px;
  color: #222;
}

.require-list__item:not(:last-child) {
  margin-bottom: 8px;
}

.require-list__item::before {
  content: "";
  display: block;
  position: absolute;
  top: 4px;
  left: -32px;
  width: 21px;
  height: 20px;
  background-size: 21px 20px;
}

