  display: block;
  width: 300px;
  height: 4px;
  position: absolute;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #1eacc7;
}

.footer__wrapper {
  display: flex;
  min-height: 142px;
  justify-content: space-between;
  align-items: center;
}

.footer__social {
  display: flex;
  flex: 0 0 25%;
}

.footer__links {
  flex: 1 1 50%;
}

.footer__store-imgs {
  flex: 0 0 25%;
  text-align: right;
}

.footer__store-imgs a {
  display: inline-flex;
  cursor: pointer;
}

.footer__store-imgs a:not(:last-child) {
  margin-right: 10px;
}

.footer-social__item {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 18px;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #070716;
}

.footer-social__item:not(:last-child) {
  margin-right: 10px;
}

.footer-social__item:hover {
  color: #fff;
}

.footer-links {
  text-align: center;
  text-transform: uppercase;
}

.footer-links__main {
  margin-bottom: 8px;
}

.footer-links__sublinks {
  margin-bottom: 8px;
}

.footer-links__link {
  font-weight: 300;
  font-size: 15px;
  color: #070716;
}

.footer-links__link:not(:first-child) {
  margin-left: 6px;
}

.footer-links__link:not(:last-child) {
  margin-right: 6px;
}

.footer-links__sublink {
  font-weight: 300;
  font-size: 11px;
  color: #999;
}

.footer-links__sublink:not(:first-child),
.footer-links__sublink:not(:last-child) {
  margin-left: 6px;
}

.footer-links__lang {
  display: block;
  font-weight: 400;
  font-size: 11px;
  color: #1eacc7;
  cursor: pointer;
}

@media (min-width: 768px) {
  .btn:hover {
    position: relative;
    bottom: 2px;
    right: 1px;
    background: linear-gradient(45deg, #09759f 0%, #41a3ee 100%);
  }
}

@media (min-width: 992px) {
  .mobile-card__link:hover {
    border: 1px solid #1eacc7;
  }
}

@media screen and (min-width: 1200px) {
  .header .nav .menu__link:hover {
    color: #1fbad6;
    padding-bottom: 2px;
  }

  .header .nav .menu__link:hover::after {
    width: 100%;
  }
}

@media (max-width: 1319.98px) {
  .footer__wrapper {
    flex-direction: column;
  }

  .footer__social {
    margin-bottom: 16px;
  }

  .footer__links {
    margin-bottom: 16px;
  }
}

@media (max-width: 1199.98px) {
  h3 {
    font-size: calc(.8rem + .6vw);
  }

  .header .nav .menu {
    padding: 12px 60px;
  }

  .header .nav .menu__item:not(:last-child) {
    padding-right: 20px;
  }

  .promo {
    padding: 188px 0 100px;
  }

  .mobile {
    padding: 38px 0 60px;
  }

  .mobile-card {
    width: 300px;
    height: 260px;
  }
}

@media (max-width: 991.98px) {
  .header .nav .menu__item:not(:last-child) {
    padding-right: 10px;
  }

  .header .nav .menu__link {
    font-size: 12px;
  }

  .subheader__official {
    font-size: 11px;
  }

  .subheader .call__link {
    font-size: 20px;
  }

  .subheader__btn {
    height: 40px;
    font-size: 13px;
  }

  .promo {
    min-height: 0;
  }

  .promo__btn {
    margin-top: 92px;
  }

  .reasons__item {
    flex-direction: column;
  }

  .reasons .item__body {
    text-align: center;
  }

  .mobile-card {
    width: 100%;
  }

  .footer__links {
    order: -1;
  }
}

@media (max-width: 991.68px) {
  .reasons .item__round {
    align-self: center;
    margin: 0 0 16px;
  }
}

@media (max-width: 767.68px) {
  .xs-hidden {
    display: none;
  }

  .xs-visible {
    display: block;
  }
}

@media (max-width: 767.98px) {
  .header .nav .menu {
    position: fixed;
    top: 0;
    left: -100%;
    width: 60%;
    height: 100vh;
    padding: 20px 15px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,.8);
    transition: .6s all;
  }

  .header .nav .menu_active {
    left: 0;
    overflow: auto;
  }

  .header .nav .menu__item:not(:last-child) {
    border: none;
    padding: 0;
    margin-bottom: 10px;
  }

  .header .nav .menu__item {
    width: 100%;
  }

  .header .nav .menu__link {
    display: block;
    padding: 10px 10px;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
  }

  .subheader__logo {
    margin-top: 8px;
  }

  .subheader__official {
    display: none;
  }

  .subheader .call__text {
    display: none;
  }

  .subheader .call__link {
    font-size: 12px;
    margin-bottom: 8px;
  }

  .subheader__btn-wrap {
    flex-direction: column-reverse;
    align-items: flex-end;
  }

  .subheader__btn {
    width: 120px;
    height: 26px;
    font-size: 10px;
  }

  .subheader {
    height: 70px;
    padding: 8px 0;
  }

  .hamburger {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    padding: 20px;
  }

  .hamburger span {
    width: 26px;
    height: 2px;
    display: block;
    background-color: #fff;
    transition: .3s all;
  }

  .hamburger span:not(:last-child) {
    margin-bottom: 4px;
  }

  .hamburger_active span {
    margin: 0;
  }

  .hamburger_active span:nth-child(1) {
    transform: rotate(45deg) translateX(5px);
  }

  .hamburger_active span:nth-child(2) {
    display: none;
  }

  .hamburger_active span:nth-child(3) {
    transform: rotate(-45deg) translateX(3px);
  }

  .require-list {
    padding-left: 32px;
  }
}

@media (max-width: 575.98px) {
  .header .nav .menu {
    width: 75%;
  }

  .promo {
    padding: 182px 0 64px;
  }

  .promo__desc {
    display: none;
  }

  .reasons .item__round {
    flex: 0 0 100px;
    width: 100px;
    height: 100px;
  }

  .choice {
    background: none;
    padding: 32px 0 64px;
  }

  .choice__black-bg {
    height: 100%;
  }

  .choice__heading,
  .choice__subheading {
    display: none;
  }

  .choice__text {
    position: relative;
    color: #fff;
  }

  .require {
    padding: 32px 0 8px;
  }

  .footer-links__sublink:not(:first-child),
  .footer-links__sublink:not(:last-child) {
    margin-left: 4px;
  }
}

@media (max-width: 374.98px) {
  .header .nav .menu {
    width: 80%;
  }

  .subheader .logo__desc {
    font-size: 12px;
  }

  .subheader {
